import React from 'react';
import s from './Post.module.css';

const Post = ({ post, deletePost}) => {
  return (
    <div className={s.item}>
      <img alt={'writer'} src='data:image/jpeg;base64,' />
      {post.text}
      <div>
        <span>like</span> ({post.likesCount})
      </div>
      <div onClick={() => deletePost(post.id)} className={s.deleteButton}></div>
    </div>
  )
}

export default Post;